.BoxCss {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
  color: #ffffff;
  background: #e74c3c;
  text-align: center;
}

@-webkit-keyframes forward-back {
  0% {-webkit-transform: translateX(0);transform: translateX(0);}
  50% {-webkit-transform: translateX(500px);transform: translateX(500px);}
  100% {-webkit-transform: translateX(0);transform: translateX(0);}
}

@keyframes forward-back {
  0% {-webkit-transform: translateX(0);transform: translateX(0);}
  50% {-webkit-transform: translateX(500px);transform: translateX(500px);}
  100% {-webkit-transform: translateX(0);transform: translateX(0);}
}

.BoxCss--animated {
  -webkit-animation: forward-back 5000ms;
          animation: forward-back 5000ms;
}


